.entry {
    background-color: rgb(1, 67, 97);
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.login {
    font-size: 1.2rem;
    color: white;
    width: 80vw;
}

.btn-user {
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 10rem;
    padding: .75rem 1rem;
    color: #fff;
    background-color: #ea4335;
    border-color: #fff;
}

.divide-container {
    font-size: 1.5em;
    display: flex;
    align-items: center;
    color: gray;
}

.short-divide {
    width: 30%;
    display: inline-block;
    border-top: 1px solid gray;
}

@media (min-width: 40rem) {
    .login {
        width: 40rem;
    }
}